/* source item */
.list_container {
    @apply !rounded-10 px-2.5 max-h-[67px] transition-all duration-400 ease-in-out;
}

.list_container:hover {
    @apply bg-primary-50 cursor-pointer;
}

.list_container:hover > .list_item {
    @apply !border-primary-50;
}

.list_container.active {
    @apply bg-primary-50 border-primary-50 max-h-[400px];
}

.list_container.active .list_item {
    @apply !border-transparent;
}

.list_container.active:hover .list_item {
    @apply bg-primary-50 cursor-pointer !rounded-10;
}

.list_container .expand-content_container {
    @apply ml-[62px] py-2.5 border-t border-white opacity-0 invisible transition-all duration-400 ease-in-out;
}

.list_container.active .expand-content_container {
    @apply opacity-100 visible;
}

.list_item {
    @apply flex items-center justify-between gap-x-3 border-b border-[#E8E7EC] py-2;
}

/* .list_item:first-child {
    @apply pt-0;
} */

/* .list_item:last-child {
    @apply pb-0 border-0;
} */

.list_item > div:nth-child(1) {
    @apply w-[50px] p-[3px];
}

.list_item > div:nth-child(1) > svg {
    @apply w-11 p-[8px] rounded-10;
}

.list_item > div:nth-child(1).cloud > svg {
    @apply bg-[#D3F6EE];
}

.list_item > div:nth-child(1).file > svg {
    @apply bg-[#FFF3D6];
}

.list_item > div:nth-child(2) {
    @apply w-[calc(100%_-_200px)];
}

.list_item > div:nth-child(3) {
    @apply w-[150px] flex justify-end relative;
}

.list_item > div:nth-child(3) > button {
    @apply w-9 h-9 block;
}

/* button */
button:disabled,
button:disabled > svg {
    @apply bg-primary-100 grayscale cursor-default pointer-events-none;
}

.running-circle {
    position: relative;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
    width: 8px;
    height: 8px;
    border: 2px solid #fff;
    border-radius: 50%;
    background: #4daa57;
}
.running-circle:after {
    content: '';
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    -webkit-animation: circle-pulse 2s infinite;
    animation: circle-pulse 2s infinite;
    border-radius: 50%;
    -webkit-box-shadow: 0 0 0 0 #4daa57;
    box-shadow: 0 0 0 0 #4daa57;
}

.stop-circle {
    position: relative;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
    width: 8px;
    height: 8px;
    border: 2px solid #fff;
    border-radius: 50%;
    background: #f50303;
}

.animate-download {
    animation: moveUpDown 2s infinite linear;
}

.loading-dots {
    position: relative;
}

.loading-dots::after {
    content: '';
    position: absolute;
    left: 100%; /* Start at the end of the text */
    padding-left: 2px; /* Space between text and dots */
    animation: dots 1.5s steps(5, end) infinite;
}

@keyframes dots {
    0% {
        content: '';
    }
    20% {
        content: '.';
    }
    40% {
        content: '..';
    }
    60% {
        content: '...';
    }
    80% {
        content: '....';
    }
    100% {
        content: '.....';
    }
}

@keyframes moveUpDown {
    0% {
        transform: translateY(0);
    }
    25% {
        transform: translateY(25%);
    }
    50% {
        transform: translateY(50%);
    }
    75% {
        transform: translateY(75%);
    }
    100% {
        transform: translateY(100%);
    }
    101% {
        transform: translateY(100%);
    }
    102% {
        transform: translateY(0);
    }
}

@keyframes circle-pulse {
    0% {
        transform: scale(0);
        opacity: 0;
        box-shadow: 0 0 0 0 rgba(77, 170, 87, 0.7);
    }
    50% {
        transform: scale(1.5); /* Increase the scale even more */
        opacity: 0.7;
        box-shadow: 0 0 0 10px rgba(77, 170, 87, 0);
    }
    100% {
        transform: scale(2); /* Further increase the scale */
        opacity: 0;
        box-shadow: 0 0 0 30px rgba(77, 170, 87, 0);
    }
}
