@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    * {
        scroll-behavior: smooth;
    }
    html {
        -webkit-tap-highlight-color: transparent;
    }
    ::-webkit-scrollbar {
        @apply w-1.5;
    }
    ::-webkit-scrollbar-thumb {
        @apply bg-secondary rounded-5;
    }
    ::-webkit-scrollbar-track {
        @apply bg-primary-50;
    }

    /* scrollbar */
    .primary-scrollbar::-webkit-scrollbar {
        @apply w-1.5;
    }
    .primary-scrollbar::-webkit-scrollbar-thumb {
        @apply bg-secondary rounded-5;
    }
    .primary-scrollbar::-webkit-scrollbar-track {
        @apply bg-primary-50;
    }
}

/* Library Css */
@import './lib-common.css';
@import './placeholder.css';
@import './inst-option.css';
@import './template.css';

/* make the custome title bar draggable */
.title-bar {
    -webkit-app-region: drag;
}
